.dialogs{
  display: grid;
  grid-template-columns: 2fr 2fr 3fr;
  grid-template-rows: 50px 1fr 1fr;
  font-family: "Roboto Light", sans-serif;
  color: whitesmoke;
  grid-template-areas:
  'h h h'
  'd a m'
;
}

.heading{
  grid-area: h;
  margin-left: 20px;
  color: #ffbb40;
  letter-spacing: 2px;
}

.dialogsItems{
  padding: 10px;
  grid-area: d;

}

.dialog{
  display: flex;
  align-items: center;
  margin: 0;

}

.dialog img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 10px;
}

.messages{
  justify-content: center;
  display: grid;
  align-items: center;
  padding: 10px;
  grid-area: m;

}

.messageAvatar{
  grid-area: a;
  border-left: 3px solid #ee0ae6;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.messageItem {
  border: 2px solid #845ec2;
  padding: 10px;
  border-radius: 12px;
  position: relative;
  max-width: 300px;
}

.left{
  border-bottom-left-radius: 0;
}

.messageItem:after{
  content: '';
  width: 1.5em;
  height: 1.5em;
  position: absolute;
  bottom: -2px;
}

.left:after{
  left: 0;
   transform: translateX(-100%);
   background: radial-gradient(circle at 0 0, transparent, transparent 72%, #845ec2 0%);
}

.messageAvatarItem{
  width: 50px;
  height: 50px;
  padding: 10px;
  border: 1px solid darkorchid;
  border-radius: 50%;
}