
.friendWrapper{
  margin: 10px;
  padding: 10px;
}

.friends h2 {
  color: #ffbb40;
}

.avatarsFriends{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
}

.avatarsFriends img{
  width: 50px;

  border-radius: 50%;
  padding: 10px;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.avatarsFriends img:hover{
  background-color: #9d7edd;

}

.avatarsFriends p {
  color: whitesmoke;
  margin: 0;
}